/* src/styles/userService/ModalFriends.module.css */

/* Ïñ¥ÎëêÏö¥ Î∞òÌà¨Î™Ö Ïò§Î≤ÑÎ†àÏù¥ */
.modalBackdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5); /* Îçî ÏûêÏó∞Ïä§Îü¨Ïö¥ Ïñ¥ÎëêÏõÄ */
  z-index: 999;
  animation: fadeIn 0.25s ease-out forwards;
}

/* ÏÇ¨Ïù¥ÎìúÎ∞î Î≥∏Ï≤¥ - Ïò§Î•∏Ï™Ω Í≥†Ï†ï, ÌÉëÎ∞î ÏïÑÎûòÎ∂ÄÌÑ∞ */
.modalContent {
  right: 5%;
  position: fixed;
  top: 64px;
  min-height: 230px;
  padding: 15px;
  width: 220px; /* ÏõêÌïòÎäî ÎÑàÎπÑ */
  max-width: 90vw;
  background-color: white;
  box-shadow: -8px 0 32px rgba(0, 0, 0, 0.15);
  z-index: 1000; /* üîë ÏàòÏ†ï: 999 -> 1000. Î∞∞Í≤ΩÎ≥¥Îã§ ÏúÑÏóê ÏúÑÏπò */
  overflow-y: auto;
  animation: slideInRight 0.35s cubic-bezier(0.16, 1, 0.3, 1) forwards;
  padding-top: 10px;
  border-radius: 0.5rem; /* Ïòà: 8px */
}

.modalContent::-webkit-scrollbar {
  width: 6px;
}

.modalContent::-webkit-scrollbar-track {
  background: transparent;
}

.modalContent::-webkit-scrollbar-thumb {
  background: #cbd5e1;
  border-radius: 3px;
}

/* Ï†úÎ™© Ïä§ÌÉÄÏùº (ÏÇ¨Ïù¥ÎìúÎ∞îÏö©ÏúºÎ°ú Ï¢Ä Îçî ÌÅ¨Í≤å) */
.modalTitle {
  font-size: 1.75rem; /* text-3xl */
  font-weight: 700;
  color: #1f2937; /* text-gray-800 */
  margin-bottom: 0.5rem;
}

/* ÏùºÎ∞ò ÌÖçÏä§Ìä∏ Î©îÏãúÏßÄ */
.modalMessage {
  color: #4b5563;
  font-size: 1rem;
  line-height: 1.6;
  text-align: center;
}

/* Î≤ÑÌäº Í∑∏Î£π */
.modalActions {
  display: flex;
  gap: 1rem;
  justify-content: center;
}

/* Î≤ÑÌäº Í≥µÌÜµ */
.cancelButton,
.deleteButton {
  flex: 1;
  padding: 0.875rem 1.5rem;
  border: none;
  border-radius: 0.5rem;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.2s;
}

/* Ï∑®ÏÜå Î≤ÑÌäº */
.cancelButton {
  background-color: #e5e7eb;
  color: #374151;
}

.cancelButton:hover {
  background-color: #d1d5db;
}

/* ÌôïÏù∏/ÏöîÏ≤≠ Î≤ÑÌäº (Í∏∞Ï°¥ Îπ®Í∞ÑÏÉâ Ïú†ÏßÄ) */
.deleteButton {
  background-color: #dc2626;
  color: white;
}

.deleteButton:hover {
  background-color: #b91c1c;
}

.deleteButton:disabled {
  background-color: #fca5a5;
  cursor: not-allowed;
}

/* Ïï†ÎãàÎ©îÏù¥ÏÖò ÌÇ§ÌîÑÎ†àÏûÑ */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideInRight {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.closeButton {
  /* Í∏∞Ï°¥ Ïä§ÌÉÄÏùº Ï†úÍ±∞ ÎòêÎäî ÏàòÏ†ï */
  font-size: 2rem; /* X ÌÅ¨Í∏∞ */
  font-weight: 300;
  line-height: 1;
  background: none;
  border: none;
  cursor: pointer;
  transition: color 0.2s;
  margin-right: 0;
  position: absolute;
  top: 0;
  right: 0;
  color: #dc2626; /* text-red-600 */
}

.closeButton:hover {
  color: #b91c1c; /* hover:text-red-700 */
}


.listItemDeleteButton {
  background-color: #dc2626; /* red-600 */
  color: white;

  border-radius: 0.25rem; /* rounded */
  font-weight: 400;
  padding: 0.1rem 0.24rem;
  transition: all 0.1s;
  margin-left: 4px;
}

.listItemDeleteButton:hover {
  background-color: #b91c1c; /* red-700 */
}