/* src/styles/layout/layout.module.css */

.navLink a {
  text-decoration: none !important;
}

.Link a {
  text-decoration: none !important;
}
.navLink {
  transition: all 0.2s ease;
  text-decoration: none !important;
  position: relative;
}

.navLink:hover {
  color: #10b981 !important;
  font-weight: 600;
  text-decoration: none !important;}

.topbar {
  /* ÏÉÅÎã® Î∞î Í∏∞Î≥∏ ÏÑ§Ï†ï */
  height: 60px; /* Ï†ÅÏ†àÌïú ÎÜíÏù¥ */
  padding: 0 16px;
  background-color: #ffffff;
  border-bottom: 1px solid #e0e0e0;

  /* FlexboxÎ•º ÏÇ¨Ïö©ÌïòÏó¨ ÎÇ¥Î∂Ä ÏöîÏÜåÎ•º Í∞ÄÎ°úÎ°ú Î∞∞Ïπò */
  display: flex;
  justify-content: space-between; /* Ïñë ÎÅùÏóê ÏöîÏÜå Î∞∞Ïπò */
  align-items: center; /* ÏàòÏßÅ Ï§ëÏïô Ï†ïÎ†¨ */

  /* Í≥†Ï†ï/Ïä§Ìã∞ÌÇ§ ÏÑ§Ï†ï (ÏÑ†ÌÉù ÏÇ¨Ìï≠) */
  position: sticky;
  top: 0;
  z-index: 100;
  text-decoration: none;

}

.leftSection {
  /* ÏôºÏ™Ω Î°úÍ≥†/Î¨∏Íµ¨ ÏòÅÏó≠ */
  font-size: 24px;
  font-weight: bold;
  color: #333333; /* Î°úÍ≥† ÏÉâÏÉÅ */
  cursor: pointer;
  text-decoration: none;
}

.rightSection {
  /* Ïò§Î•∏Ï™Ω Î≤ÑÌäº/ÏïÑÏù¥ÏΩò ÏòÅÏó≠ */
  display: flex;
  align-items: center;
  gap: 15px; /* Î≤ÑÌäº/ÏïÑÏù¥ÏΩò Í∞ÑÍ≤© */
  text-decoration: none;
}

.rightItem {
  /* Í∞úÎ≥Ñ ÏïÑÏù¥ÏΩò/Î≤ÑÌäº Ïä§ÌÉÄÏùº */
  font-size: 16px;
  color: #666666;
  cursor: pointer;
  padding: 5px 8px; /* ÌÅ¥Î¶≠ ÏòÅÏó≠ ÌôïÎ≥¥ */
  transition: color 0.2s;
  text-decoration: none;


}

.rightItem:hover {
  color: #000000; /* Ìò∏Î≤Ñ Ïãú ÏÉâÏÉÅ Î≥ÄÌôî */
}

/* Log ÏûëÏÑ± Î≤ÑÌäº Ïä§ÌÉÄÏùº (Í∞ïÏ°∞) */
/* Í∏∞Ï°¥ Ïä§ÌÉÄÏùºÏóê Ï∂îÍ∞Ä */

.profileSection {
  display: flex;
  align-items: center;
  margin: 0 8px;
}

.profileImage {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #e0e0e0;
  transition: all 0.3s ease;
}

.profileImage:hover {
  border-color: #6366f1;
  transform: scale(1.05);
}

.profileCircle {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 600;
  font-size: 16px;
  border: 2px solid #e0e0e0;
  transition: transform 0.3s ease;
}

.profileCircle:hover {
  transform: scale(1.05);
}
/* styles/layout/layout.module.css Ïóê Ï∂îÍ∞Ä */

/* ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ/ÏïÑÏù¥ÏΩòÍ≥º ÎìúÎ°≠Îã§Ïö¥ÏùÑ Í∞êÏã∏Îäî Ïª®ÌÖåÏù¥ÎÑà */
.profileContainer {
  position: relative; /* ÎìúÎ°≠Îã§Ïö¥ÏùÑ Ïù¥ ÏöîÏÜåÎ•º Í∏∞Ï§ÄÏúºÎ°ú ÏúÑÏπòÏãúÌÇ§Í∏∞ ÏúÑÌï¥ ÌïÑÏöî */
  /* topbarÏùò rightSection ÎÇ¥ÏóêÏÑú Í≥µÍ∞ÑÏùÑ Ï∞®ÏßÄÌïòÎèÑÎ°ù flex ÏÜçÏÑ±ÏùÑ Ï°∞Ï†ïÌï† Ïàò ÏûàÏäµÎãàÎã§. */
}

/* ÎìúÎ°≠Îã§Ïö¥ Î©îÎâ¥ Ïä§ÌÉÄÏùº */
.profileDropdown {
  position: absolute;
  top: 100%; /* profileContainer Î∞îÎ°ú ÏïÑÎûòÏóê ÏúÑÏπò */
  right: 0;
  width: 200px; /* ÏõêÌïòÎäî ÎÑàÎπÑ */
  background-color: white;
  border: 1px solid #ddd;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 1000; /* Îã§Î•∏ ÏöîÏÜå ÏúÑÏóê ÌëúÏãúÎêòÎèÑÎ°ù ÎÜíÍ≤å ÏÑ§Ï†ï */
  border-radius: 4px;
  margin-top: 8px; /* ÌîÑÎ°úÌïÑ ÏïÑÏù¥ÏΩòÍ≥º Í∞ÑÍ≤© */
  overflow: hidden; /* Î≤ÑÌäº border-radiusÎ•º ÏúÑÌï¥ ÌïÑÏöî */
  display: flex;
  flex-direction: column;
}

.dropdownHeader {
  padding: 10px 15px;
  border-bottom: 1px solid #eee;
  color: #333;
  font-size: 0.9rem;
}

.dropdownItem {
  padding: 10px 15px;
  background: none;
  border: none;
  text-align: left;
  cursor: pointer;
  font-size: 0.9rem;
  color: #555;
  width: 100%;
}

.dropdownItem:hover {
  background-color: #f5f5f5;
  color: #007bff;
}